<script>
import io from "socket.io-client";
 const socket = io("https://stopover-chat.herokuapp.com"); // /!\ basculer dev / prod
//  const socket = io("http://localhost:3000");
export default {
  data() {
    return {
      connectedAt : null,
      text: null,
      messages: [],
      users: [],
      isAdmin: false,
      username : null,
      roomName : null,
      showMenu: false,
      sendDisabled: false,
      inputDisabled : false,
      timer: "",
      expireAt: null,
      isTyping: [],
      isBanned: false,
      modalActive: false,
      adminMsg: null,
      userBanned : []
    };
  },
  created() {
    socket.emit("USER_JOIN", {
      username: this.$route.query.user,
      room: this.$route.query.name,
      ip : this.$store.state.ip
    });
    this.username = this.$route.query.user;
    this.roomName = this.$route.query.name;
    this.connectedAt = new Date().getTime()
  },
  mounted() {
    socket.on("JOIN", data => {
      this.messages.push({ alert: data.alert });
      if (data.expiration) { 
        this.expireAt = data.expiration;
        this.setTimer();
        this.$cookies.set(`${this.$route.query.name}`, "login", new Date(`${this.expireAt}`));
      }  
      if (data.blocked && data.blocked.user === this.$route.query.user) {
        this.inputDisabled = true;
        this.sendDisabled = true;
      }
      if (data.admin === this.$route.query.user) {
        this.isAdmin = true;
      }
      if (data.ban) {
        this.userBanned= data.ban
      }
    });
    socket.on("UPDATE_STATUS", data => {
      this.users = data.users;
    });
    socket.on("USERS_CONNECTED", data => {
      this.users = data.users;
    });
    socket.on("MESSAGE", data => {
      this.messages.push(data);
      // this.$refs.input.focus();
    });
    socket.on("LEAVE", data => {
      this.messages.push({ alert: data.alert });
      this.users = data.users;
    });
    socket.on("LOG_OUT", data => {
      this.$router.replace(data.redirect);
    });
    socket.on("IS_TYPING", data => {
      const index = this.isTyping.indexOf(data.username);
      if (index != -1) {
        this.isTyping.splice(index, 1);
      }
      if (data.length != 0) this.isTyping.push(data.username);
    });
    socket.on("BAN_RESPONSE", data => {
      if (data.user === this.$route.query.user) {
        socket.disconnect();
        this.isBanned = true;
        this.modalActive = true;
        this.adminMsg = `You have been baned by ${data.admin}.`;
      } else {
        this.modalActive = true;
        this.adminMsg = `${data.user} has been baned by ${data.admin}.`;
      }
      this.userBanned = data.ban
    });
    socket.on("UNBAN_RESPONSE", data => {
        this.modalActive = true;
        this.adminMsg = `${data.user} has been unbanned by ${data.admin}.`;
        this.userBanned = data.ban
    })
    socket.on("BLOCK_RESPONSE", data => {
      if (data.user === this.$route.query.user) {
        this.modalActive = true;
        this.sendDisabled = true;
        this.inputDisabled = true
        this.adminMsg =
          `You have been blocked by ${data.admin}. <br>
          You can read messages but not send its`;
      } else {
        this.modalActive = true;
        this.adminMsg = `${data.user} has been blocked by ${data.admin}.`;
      }
      this.users = data.users;
    });
    socket.on("UNBLOCK_RESPONSE", data => {
      if (data.user === this.$route.query.user) {
        this.modalActive = true;
        this.sendDisabled = false;
        this.inputDisabled = false;
        this.adminMsg =
          `You have been unblocked by ${data.admin}. <br>
          You can know send messages again !`;
      } else {
        this.modalActive = true;
        this.adminMsg = `${data.user} has been unblocked by ${data.admin}.`;
      }
      this.users = data.users;
    })
  },
  methods: {
    closeModal() {
      if (this.isBanned === true) {
        socket.connect(); // cela est nécessaire pour permettre la reconnection à la salle
        this.$router.replace('/')
      } else {
        this.modalActive = false;
      }
    },
    send() {
      if (this.text.length > 0)
      this.sendDisabled = true;
      socket.emit(
        "SEND_MESSAGE",
        {
          room: this.$route.query.name,
          user: this.$route.query.user,
          message: this.text
        },
        () => {
          // expecting aknowlegment from server
          this.sendDisabled = false;
        }
      );
      this.text = null;
    },
    setTimer() {
      /*SET TIMER */
      // Set the date we're counting down to
      const countDownDate = new Date(`${this.expireAt}`);

      // countDownDate.setDate(countDownDate.getDate()+1);

      const interval = setInterval(() => {
        // Update the count down every 1 second

        const now = new Date().getTime(); // Get today's date and time

        const distance = countDownDate - now; // Find the distance between now and the count down date

        const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));// Time calculations for days, hours, minutes and seconds
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);

        this.timer = `${hours}h ${minutes}m  ${seconds}s `;

        if (hours === 0 && minutes === 0 && seconds === 0) {
          socket.disconnect();
          clearInterval(interval);
          this.modalActive = true;
          this.isBanned = true;
          this.adminMsg = "Your session has expired"
        }

      }, 1000);
    },
    setStatus(status) {
      socket.emit("SET_STATUS", {
        room: this.$route.query.name,
        user: this.$route.query.user,
        status: status
      });
    },
    logout() {
      socket.emit("LOGOUT");
    },
    ban(user, ip) {
      socket.emit("BAN_REQUEST", {
        room: this.$route.query.name,
        user,
        ip,
        admin: this.$route.query.user
      });
    },
    unban(user, ip) {
       socket.emit("UNBAN_REQUEST", {
        room: this.$route.query.name,
        user,
        ip,
        admin: this.$route.query.user
      });
    },
    block(user, ip, event) {
      if (event.target.textContent === "Block") {
        socket.emit("BLOCK_REQUEST", {
        room: this.$route.query.name,
        user,
        ip,
        admin: this.$route.query.user
      });
      } else {
        socket.emit("UNBLOCK_REQUEST", {
        room: this.$route.query.name,
        user,
        admin: this.$route.query.user
      });
      }
    }
  },
  watch: {
    text(val) {
      if(val != null) {
        socket.emit("TYPING", {
        length: val.length,
        username: this.$route.query.user,
        room: this.$route.query.name
      });
      }
      else if (val == null) {
       
        socket.emit("TYPING", {
        length: 0,
        username: this.$route.query.user,
        room: this.$route.query.name
      }); 
    }
  },
  }
};


</script>

